const PhaserCommon = {
    data: function() {
        return {
            nodeType: Object.freeze({
                PHASE: "phase",	
                GROUP: "group",
                SUBGROUP: "subgroup",
                CONTEXT: "context"
            }),
        }
    }, 
    methods: {
        getJSON(uri, success=()=>{}, error=()=>{}) {
            let options = {
                method: 'GET', 
                headers: { 
                    'Accept': 'application/json',
                    'Content-Type': 'application/json' 
                } 
            }
            fetch(uri, options)
                .then(response => response.json())
                .then(success)
                .catch(error)
        }
    }
}
export default PhaserCommon
