<template>
    <b-container fluid>
		<b-row>			
			<b-col></b-col>
			<b-col>
				<b-form-group
					label="Filter"
					label-for="filter-input"
					label-cols-sm="3"
					label-align-sm="right"
					label-size="sm"
					class="mb-2">
					<b-form-input 
						size="sm"
						id="filter-input"
						v-model="filter"
						type="search"
						:placeholder="`filter ${itemsClass}s`"/>					
			</b-form-group>
			</b-col>
		</b-row>
		<b-table show-empty
			id="datatable"
			sort-icon-left 
			striped small hover outlined
			sticky-header="300px" 
			primary-key="id"
			:items="items" 
			:fields="fields"
			:filter="filter" 			
			class="overflow-auto">
			<template #cell(actions)="row">
				<div class="text-right">										
					<b-icon-x-circle
						class="action mr-2" 
						:title="`delete ${itemsClass}`" 
						:alt="`delete ${itemsClass}`"							
						@click.stop="deleteItem(row.item)"/>					
				</div>				
			</template>
		</b-table>
		<div class="text-right">
			<b-icon-plus-circle
				class="action mr-2" 
				:title="`add ${itemsClass}`" 
				:alt="`add ${itemsClass}`"			
				@click.stop="insertItem()"/>			
		</div>
	</b-container>
</template>

<script>
export default {
	name: 'ItemTable',
	components: {},
	props: {
		items: {
			type: Array,
			required: true
		},
		fields: {
			type: Array,
			required: true
		},
		itemsType: {
			type: String,
			required: false,
			default: "item",
			validator: value => ["item", "phase", "group", "subgroup", "context"].includes(value)      
		}
	},
	data() { 
		return {
			filter: "",	
			itemsClass: this.itemsType		
		}
	},
	computed: {			
       totalRows() { return this.items.length }
	},
	mounted() {},
	methods: {		
		insertItem(){},

		updateItem(){},
			
		deleteItem(item){
			let self = this
			self.$bvModal.msgBoxConfirm(`Delete ${ this.itemsClass } ${item.identifier} - are you sure?`)
				.then(value => {
					if(value) self.$store.dispatch('removeNode', item)
				})		
		},	
		
		editID: function(e, item){
			const value = e.target.innerText; 
			// has anything changed?	
			if(item.id !== value) {
				item.id = value;			
				this.edit(item);
			}
        },	
		editType: function(e, item){
			const value = e.target.innerText;
			// has anything changed?
			if(item.type !== value) {
				item.type = value;
				this.edit(item);
			}
		}		
	}
} 
</script>
<style scoped>
.action { 
	cursor: pointer;
	color:dodgerblue;
}
.action:hover {
	color:red;
}
</style>

